<template lang='pug'>
    div
        el-card.search
            el-form(ref='flight', :model='flight')
                el-row(:gutter='10', type='flex', justify='center', style='width: 1000px; margin: auto')
                    el-col(:span='6')
                        el-form-item
                            cityCard(placeholder='输入国家/城市/机场', v-model='flight.depart', @itemSelect='departSelect')
                    el-col(:span='1')
                        el-form-item(style='text-align: center')
                            el-button.exchange(round, size='small', @click='cityExchange') 换
                    el-col(:span='6')
                        el-form-item
                            cityCard(placeholder='输入国家/城市/机场', v-model='flight.arrive', @itemSelect='arriveSelect')
                    el-col(:span='6')
                        el-form-item
                            el-date-picker(type='date', v-model='flight.setTime', placeholder='出发日期')
                    el-col(:span='2')
                        el-form-item
                            el-button(type='success', @click='flightSearch', size='medium') 重新搜索
        el-card.screening
            el-form(ref='flight_opt', :model='flight_opt')
                el-row(:gutter='10')
                    el-col(:span='8')
                        el-select(v-model='flight_scn.setTime', filterable, multiple, placeholder='起飞时段', size='small', @change='screening')
                            el-option(v-for='item in flight_opt.setTime', :value='item.value', :key='item.value', :label='item.label')
                    el-col(:span='8')
                        el-select(v-model='flight_scn.airline', filterable, multiple, placeholder='航空公司', size='small', @change='screening')
                            el-option(v-for='item in flight_opt.airline', :value='item', :key='item', :label='airline.get(item)')
                    //- el-col(:span='4')
                    //-     el-select(v-model='flight_scn.depart', filterable, multiple, placeholder='起飞机场', size='small')
                    //-         el-option(v-for='item in flight_opt.depart', :value='item.value', :key='item.value') {{item.label}}
                    //- el-col(:span='4')
                    //-     el-select(v-model='flight_scn.arrive', filterable, multiple, placeholder='降落机场', size='small')
                    //-         el-option(v-for='item in flight_opt.arrive', :value='item.value', :key='item.value') {{item.label}}
                    el-col(:span='8')
                        el-select(v-model='flight_scn.position', filterable, multiple, placeholder='仓位', size='small')
                            el-option(v-for='item in flight_opt.position', :value='item.value', :key='item.value', :label='item.label')
        .main-data
            div.flight_table(v-if='flight_data.length>0')
                template(v-for='(item, index) in flight_data_show')
                    flight-card(:f_data='item', :ow_data='ow_data[item.no]')
            div(v-else, style='width: 100%;height: 500px;', v-loading='true', element-loading-text="数据加载中", element-loading-spinner="el-icon-loading")
</template>

<script>
import flightCard from './FlightCard.vue'
import cityCard from '../../common/CityCard.vue'
export default {
    components: {flightCard, cityCard},
    data () {
        return {
            flight: {
                depart: '',
                arrive: '',
                setTime: '',
                theDepart: {},
                theArrive: {}
            },
            flight_opt: {
                airline: [],
                setTime: [
                    {
                        value: '00:00-10:00',
                        label: '00:00-10:00'
                    },
                    {
                        value: '10:00-14:00',
                        label: '10:00-14:00'
                    },
                    {
                        value: '14:00-19:00',
                        label: '14:00-19:00'
                    },
                    {
                        value: '19:00-24:00',
                        label: '19:00-24:00'
                    }
                ],
                position: [
                    {
                        value: 'economy',
                        label: '经济舱'
                    },
                    {
                        value: 'business',
                        label: '公务/头等舱'
                    }
                ],
                depart: [
                    {
                        value: '北京南苑机场',
                        label: '北京南苑机场'
                    },
                    {
                        value: '北京首都机场',
                        label: '北京首都机场'
                    }
                ],
                arrive: [
                    {
                        value: '上海虹桥机场',
                        label: '上海虹桥机场'
                    },
                    {
                        value: '上海浦东机场',
                        label: '上海浦东机场'
                    }
                ]
            },
            flight_scn: {
                airline: [],
                setTime: [],
                position: [],
                depart: [],
                arrive: []
            },
            airline: new Map([['3U', '四川航空'], ['8L', '祥鹏航空'], ['9C', '春秋航空'], ['9H', '长安航空'], ['A6', '红土航空'], ['BK', '奥凯航空'], ['CA', '中国航空'], ['CN', '大新华航'], ['CZ', '南方航空'], ['DR', '瑞丽航空'], ['DZ', '东海航空'], ['EU', '成都航空'], ['FM', '上海航空'], ['FU', '福州航空'], ['G5', '华夏航空'], ['GJ', '长龙航空'], ['GS', '天津航空'], ['GT', '桂林航空'], ['GX', '北部弯航空'], ['GY', '多彩贵州航空'], ['HO', '吉祥航空'], ['HU', '海南航空'], ['JD', '首都航空'], ['JR', '幸福航空'], ['KN', '中联航空'], ['KY', '昆明航空'], ['LT', '龙江航空'], ['MF', '厦门航空'], ['MU', '东方航空'], ['NS', '河北航空'], ['OQ', '重庆航空'], ['PN', '西部航空'], ['QW', '青岛航空'], ['RY', '江西航空'], ['SC', '山东航空'], ['TV', '西藏航空'], ['UQ', '乌鲁木齐航空'], ['Y8', '扬子江快运航空'], ['YI', '英安航空'], ['ZH', '深圳航空']]),
            flight_data: [],
            flight_data_show: [],
            ow_data: {},
            ow_data_show: {}
        }
    },
    methods: {
        departSelect (item) {
            console.log(item)
            this.flight.theDepart = item
        },
        arriveSelect (item) {
            console.log(item)
            this.flight.theArrive = item
        },
        cityExchange () {
            let a = ''
            let b = {}
            a = this.flight.depart
            this.flight.depart = this.flight.arrive
            this.flight.arrive = a
            b = this.flight.theDepart
            this.flight.theDepart = this.flight.theArrive
            this.flight.theArrive = b
        },
        clear () {
            this.flight_data = []
            this.flight_data_show = []
            this.ow_data = []
            this.ow_data_show = []
            this.flight_opt.airline = []
            this.flight_scn.airline = []
            this.flight_scn.setTime = []
            this.flight_scn.position = []
        },
        screening () {
            this.flight_data_show = []
            let arr = this.flight_data
            for (let i in arr) {
                if (this.scnAirline(arr[i].airline) && this.scnTime(arr[i].departTime)) {
                    this.flight_data_show.push(arr[i])
                }
            }
        },
        scnAirline (airline) {
            if (this.flight_scn.airline.length === 0 || this.flight_scn.airline.indexOf(airline) !== -1) {
                return true
            } else {
                return false
            }
        },
        scnTime (time) {
            if (this.flight_scn.setTime.length === 0) {
                return true
            } else {
                let setTime = this.flight_scn.setTime
                for (let i in setTime) {
                    switch (setTime[i]) {
                    case '00:00-10:00':
                        if (time.substring(0, 2) < 10) {
                            return true
                        }
                        break
                    case '10:00-14:00':
                        if (time.substring(0, 2) >= 10 && time.substring(0, 2) < 14) {
                            return true
                        }
                        break
                    case '14:00-19:00':
                        if (time.substring(0, 2) >= 14 && time.substring(0, 2) < 19) {
                            return true
                        }
                        break
                    case '19:00-24:00':
                        if (time.substring(0, 2) >= 19 && time.substring(0, 2) < 24) {
                            return true
                        }
                        break
                    default: return false
                    }
                }
            }
        },
        flightSearch () {
            this.clear()
            let _this = this
            if (this.flight.depart === '' || this.flight.arrive === '') {
                this.$Message.warning('城市选项不能为空')
            } else if (this.flight.setTime === '') {
                this.$Message.warning('请输入出发时间')
            } else if (this.flight.depart === this.flight.arrive) {
                this.$Message.warning('到达城市不能与出发城市相同')
            } else {
                let time = this.flight.setTime.getFullYear() + '-' + (this.flight.setTime.getMonth() + 1) + '-' + this.flight.setTime.getDate()
                this.$http.get('/jp/searchPlaneTicket', {
                    params: {
                        depart: _this.flight.theDepart.cityCode,
                        arrive: _this.flight.theArrive.cityCode,
                        date: time
                    }
                }).then(function (res) {
                    console.log(res.data)
                    _this.ow_data = res.data.ow_data
                    for (let i in res.data.flights) {
                        _this.flight_data.push(res.data.flights[i])
                    }
                    for (let i = 0; i < _this.flight_data.length; i++) {
                        if (_this.flight_data[i].depart === 'NAY') {
                            _this.flight_data[i].departPort = '北京南苑机场'
                        } else if (_this.flight_data[i].depart === 'SHA') {
                            _this.flight_data[i].departPort = '上海虹桥机场'
                        } else {
                            _this.flight_data[i].departPort = _this.flight.theDepart.airportNameCN
                        }

                        if (_this.flight_data[i].arrive === 'NAY') {
                            _this.flight_data[i].arrivePort = '北京南苑机场'
                        } else if (_this.flight_data[i].arrive === 'SHA') {
                            _this.flight_data[i].arrivePort = '上海虹桥机场'
                        } else {
                            _this.flight_data[i].arrivePort = _this.flight.theArrive.airportNameCN
                        }
                    }
                    for (let i in _this.flight_data) {
                        if (_this.flight_opt.airline.indexOf(_this.flight_data[i].airline) === -1) {
                            _this.flight_opt.airline.push(_this.flight_data[i].airline)
                        }
                    }
                    _this.flight_data_show = _this.flight_data
                }).catch(function (error) {
                    console.log(error)
                })
            }
        }
    },
    created () {
        let _this = this
        this.flight.setTime = new Date()
        let time = this.flight.setTime.getFullYear() + '-' + (this.flight.setTime.getMonth() + 1) + '-' + this.flight.setTime.getDate()
        this.flight.depart = '北京(BJS)'
        this.flight.arrive = '上海(SHA)'
        this.flight.theDepart = {
            airportCode: 'PEK',
            airportNameCN: '北京首都国际机场',
            airportNameEN: 'beijing',
            cityCode: 'BJS',
            cityNameCN: '北京',
            cityNameEN: 'beijing',
            cityPinyin: 'beijing',
            cityPy: 'bj',
            isHot: 'Y'
        }
        this.flight.theArrive = {
            airportCode: 'PVG',
            airportNameCN: '上海浦东机场',
            airportNameEN: 'Shanghai',
            cityCode: 'SHA',
            cityNameCN: '上海',
            cityNameEN: 'Shanghai',
            cityPinyin: 'shanghai',
            cityPy: 'sh',
            isHot: 'Y'
        }
        let queryObj = this.$route.query
        console.log(typeof queryObj.depart === 'object' && typeof queryObj.arrive === 'object')
        if (typeof queryObj.depart === 'object' && typeof queryObj.arrive === 'object') {
            this.flight.depart = queryObj.depart.cityNameCN + '(' + queryObj.depart.cityCode + ')'
            this.flight.arrive = queryObj.arrive.cityNameCN + '(' + queryObj.arrive.cityCode + ')'
            this.flight.setTime = queryObj.time
            time = this.flight.setTime
            this.flight.theDepart = queryObj.depart
            this.flight.theArrive = queryObj.arrive
        }
        this.$http.get('/jp/searchPlaneTicket', {
            params: {
                depart: _this.flight.theDepart.cityCode,
                arrive: _this.flight.theArrive.cityCode,
                date: time
            }
        }).then(function (res) {
            console.log(res.data)
            _this.ow_data = res.data.ow_data
            for (let i in res.data.flights) {
                _this.flight_data.push(res.data.flights[i])
            }
            for (let i = 0; i < _this.flight_data.length; i++) {
                if (_this.flight_data[i].depart === 'NAY') {
                    _this.flight_data[i].departPort = '北京南苑机场'
                } else if (_this.flight_data[i].depart === 'SHA') {
                    _this.flight_data[i].departPort = '上海虹桥机场'
                } else {
                    _this.flight_data[i].departPort = _this.flight.theDepart.airportNameCN
                }

                if (_this.flight_data[i].arrive === 'NAY') {
                    _this.flight_data[i].arrivePort = '北京南苑机场'
                } else if (_this.flight_data[i].arrive === 'SHA') {
                    _this.flight_data[i].arrivePort = '上海虹桥机场'
                } else {
                    _this.flight_data[i].arrivePort = _this.flight.theArrive.airportNameCN
                }
            }
            for (let i in _this.flight_data) {
                if (_this.flight_opt.airline.indexOf(_this.flight_data[i].airline) === -1) {
                    _this.flight_opt.airline.push(_this.flight_data[i].airline)
                }
            }
            _this.flight_data_show = _this.flight_data
        }).catch(function (error) {
            console.log(error)
        })
    }
}
</script>

<style scoped>
    .search {
        min-width: 1000px;
        margin: 0 auto 10px;
        overflow: visible;
        text-align: center;
        position: fixed;
        left: 200px;
        right: 0px;
        border-radius: 0;
        top: 60px;
        z-index: 10000;
    }
    .screening {
        width: 1000px;
        margin: 0 auto;
        border-radius: 0;
        margin-top: 100px;
    }
    .main-data {
        width: 1000px;
        margin: 0 auto;
        /* background: red; */
    }
    .exchange {
        padding: 6px;
        font-family: "幼圆";
        color: #352b2b;
        background-color: #f5eded;
    }
    .exchange:hover {
        color: #409eff;
        background-color: #fff;
    }
</style>



