<template lang='pug'>
    div
        .airfly(:class='{"actived": show}')
            .airfly__card(@click='show=!show')
                .airfly__card_name
                    .c-company
                        .flight-type
                            span.logo
                                img(:src='f_data.logo')
                            span.title
                                |{{airline.get(f_data.airline)}}
                        .c-company__detail
                            span {{f_data.no}}
                            span 型号{{f_data.plane}}
                            span.shared(v-if='f_data.shared')
                                el-tooltip.item(effect='dark', :content="'实际乘坐: ' + airline.get(f_data.operatingNo.substr(0, 2)) + f_data.operatingNo", palcement='bottom-start')
                                    span 共享
                .airfly__card_time
                    .sep-lf
                        h2 {{f_data.departTime}}
                        p {{f_data.departPort}}{{f_data.departTerminal}}
                    .sep-ct
                        .range {{f_data.flightTime}}
                        .line
                        p.nonstop(v-if='!f_data.nonstop') 非直达
                    .sep-rt
                        h2 {{f_data.arriveTime}}
                        p {{f_data.arrivePort}}{{f_data.arriveTerminal}}
                .airfly__card_price
                    p.prc
                        span.rmb ￥
                        span.data {{ow_data.minFdPrice}}
                    .vim
                        span.dis {{f_data.dis}}
            el-collapse-transition
                .airfly__extends(v-show='show')
                    .airfly__extends_list
                        .c-triangle
                            p
                        //- .flight-type
                        //-     ul.tab
                        //-         li(:class="{'cur': all_space}") 全部
                                //- li(:class="{'cur': !all_space}", @click='change_space(false)') 头等/商务舱
                        .flight-info(v-if='stock>0 || stock==="A"')
                            .flight-info__list
                                ul
                                    li 准点率 {{f_data.onTimeRate}}
                                    li(v-if='f_data.meals==="B"') 餐食：早餐
                                    li(v-else-if='f_data.meals==="D"') 餐食：正餐
                                    li(v-else-if='f_data.meals==="M"') 餐食：轻正餐
                                    li(v-else-if='f_data.meals==="S"') 餐食：点心餐
                                    li(v-else-if='f_data.meals==="R"') 餐食：快餐
                                    li(v-else-if='f_data.meals!==null') 有餐食{{f_data.meals}}
                                    li(v-else) 无餐食
                                    li 以下价格不包括机建燃油费
                            .flight-info__order
                                .order__card
                                    .order__card_col(v-for='item in ow_data.cabins', v-if='item.stock>0 || item.stock==="A"')
                                        .col-agent
                                            span(v-if='item.level==="Y"') 经济
                                            span(v-else-if='item.level==="F"') 头等
                                            span(v-else='item.level==="C"') 商务
                                            span {{item.cabin}}舱
                                        .col-center
                                            .col-center__tag
                                                .col-center__tag_tips
                                                    span 退改签
                                                .col-center__tag_tips
                                                    span 行李额说明
                                            .col-center__prc
                                                el-row
                                                    el-col(:span='6')
                                                        span.col-center__prc_logo ￥
                                                        span.col-center__prc_prc {{item.minParPrice}}
                                                    el-col(:span='4')
                                                        span.col-center__prc_ret 返现：{{item.minParPrice-item.minSalePrice}}
                                                    el-col(:span='6')
                                                        span.col-center__prc_discount 折扣:{{item.discount}}
                                                    el-col(:span='6')
                                                        span.col-center__prc_stock 余票:{{item.stock === "A" ? "余票充足" : item.stock}}
                                        .col-book
                                            el-button(type='success', @click='orderTicket(item)') 预定
                        .flight-info__else(v-else)
                            p 没有余票
</template>

<script>
export default {
    data () {
        return {
            show: false,
            all_space: true,
            airline: '',
            stock: 0
        }
    },
    props: {
        f_data: Object,
        ow_data: Object
    },
    methods: {
        change_space (bool) {
            this.all_space = bool
        },
        orderTicket (item) {
            let time = new Date(this.f_data.departDate)
            let cardObj = {
                departDate: time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate(),
                departCityName: this.f_data.departPort.substr(0, 2),
                arriveCityName: this.f_data.arrivePort.substr(0, 2),
                airline: this.airline.get(this.f_data.airline),
                no: this.f_data.no,
                cabin: item.cabin,
                level: item.level,
                departTime: this.f_data.departTime,
                arriveTime: this.f_data.arriveTime,
                departPort: this.f_data.departPort + this.f_data.departTerminal,
                arrivePort: this.f_data.arrivePort + this.f_data.arriveTerminal,
                flightTime: this.f_data.flightTime,
                price: {
                    'policyId': item.salePrices[0].policyId, // 7;产品ID,在选中的航班中有该数据;
                    'ageType': '', // 8;年龄类型 成人ADT 儿童CHD 婴儿INF
                    'cabin': item.cabin, // 9;舱位
                    'fdPrice': item.fdPrice, // 10;展示价格
                    'sequence': 1,
                    'fuelFee': this.f_data.fuelFee,
                    'taxFee': this.f_data.taxFee
                }
            }
            let orderObj = {
                cName: '',
                cPhone: '',
                flights: [
                    {
                        depart: this.f_data.depart, // 3;起点三字码
                        arrive: this.f_data.arrive, // 4;终点三字码
                        departDate: this.f_data.departDate, // 5;起飞时间:'2018-02-09 00:00:00'
                        flightNo: this.f_data.no, // 6;飞机航班号:'MU2862'
                        sequence: 1
                    }
                ],
                prices: [
                    // {
                    //     'policyId': item.salePrices[0].policyId,              // 7;产品ID,在选中的航班中有该数据;
                    //     'ageType': '',              // 8;年龄类型 成人ADT 儿童CHD 婴儿INF
                    //     'item': item.cabin,                // 9;舱位
                    //     'fdPrice': item.fdPrice,               // 10;展示价格
                    //     'fuelFee': this.f_data.fuelFee,
                    //     'taxFee': this.f_data.taxFee
                    // }
                ],
                travelers: [
                    // {
                    //     'ageType': '',              // 11;年龄类型;
                    //     'name': '',                 // 12;乘客姓名;
                    //     'cardType': '',             // 13;证件类型:身份证NI 护照PP 回乡证HX 台胞证TB 港澳通行证GA 国际海员证HY
                    //     'cardNo': '',               // 14;证件号
                    //     'phone': ''                // 15;手机
                    // }
                ]
            }
            // console.log(orderObj)
            this.$router.push({path: '/flightOrder', query: {orderObj: orderObj, cardObj: cardObj}})
        }
    },
    created () {
        let airline = new Map([['3U', '四川航空'], ['8L', '祥鹏航空'], ['9C', '春秋航空'], ['9H', '长安航空'], ['A6', '红土航空'], ['BK', '奥凯航空'], ['CA', '中国航空'], ['CN', '大新华航'], ['CZ', '南方航空'], ['DR', '瑞丽航空'], ['DZ', '东海航空'], ['EU', '成都航空'], ['FM', '上海航空'], ['FU', '福州航空'], ['G5', '华夏航空'], ['GJ', '长龙航空'], ['GS', '天津航空'], ['GT', '桂林航空'], ['GX', '北部弯航空'], ['GY', '多彩贵州航空'], ['HO', '吉祥航空'], ['HU', '海南航空'], ['JD', '首都航空'], ['JR', '幸福航空'], ['KN', '中联航空'], ['KY', '昆明航空'], ['LT', '龙江航空'], ['MF', '厦门航空'], ['MU', '东方航空'], ['NS', '河北航空'], ['OQ', '重庆航空'], ['PN', '西部航空'], ['QW', '青岛航空'], ['RY', '江西航空'], ['SC', '山东航空'], ['TV', '西藏航空'], ['UQ', '乌鲁木齐航空'], ['Y8', '扬子江快运航空'], ['YI', '英安航空'], ['ZH', '深圳航空']])
        this.airline = airline
        for (let item in this.ow_data.cabins) {
            if (this.ow_data.cabins[item].stock === 'A') {
                this.stock = 'A'
                break
            }
            if (this.ow_data.cabins[item].stock > 0) {
                this.stock += parseInt(this.ow_data.cabins[item].stock)
            }
        }
    }
}
</script>

<style scoped>
    .airfly {
        border-bottom: 1px solid rgb(207, 207, 207);
        background-color: #fff;
    }
    .airfly.actived {
        margin: 10px 0;
        position: relative;
        box-shadow: 0 0 15px rgba(0,0,0,.2);
    }
    .airfly:hover {
        position: relative;
        box-shadow: 0 0 15px rgba(0,0,0,.2);
    }
    .airfly__card {
        box-sizing: border-box;
        background-color: #fbfbfb;
        height: 100px;
        width: 1000px;
        padding: 25px 30px 15px 15px;
        /* margin-bottom: 10px; */
        display: flex;
        /* justify-content: space-between; */
        cursor: pointer;
    }
    .airfly__card:focus {
        outline: -webkit-focus-ring-color auto 5px;
    }
    /* .c-company----------------------------------- */
    .c-company > .flight-type > span {
        display: inline-block;
    }
    .c-company > .flight-type > .logo {
        width: 34px;
    }
    .c-company > .flight-type > .title {
        font-size: 16px;
        height: 24px;
        margin-bottom: 3px;
    }
    .c-company > .c-company__detail {
        padding: 0 0 3px 34px;
        color: #888;
    }
    .c-company > .c-company__detail > span {
        padding-right: 4px;
        display: inline-block;
    }
    .c-company__detail > span.shared {
        color: #46d2a9
    }
    .airfly__card_name {
        width: 300px;
    }
    /* .airfly__card_time----------------------------------- */
    .airfly__card_time {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        width: 500px;
    }
    .airfly__card_time > .sep-lf, .airfly__card_time > .sep-rt {
        width: 190px;
        text-align: center;
    }
    .airfly__card_time > .sep-lf > h2, .airfly__card_time > .sep-rt > h2 {
        font-size: 25px;
    }
    .airfly__card_time > .sep-ct {
        color: #888;
        width: 120px;
        text-align: center;
        margin: 0 23px;
    }
    .airfly__card_time > .sep-ct > .range {
        height: 20px;
    }
    .airfly__card_time > .sep-ct > .line {
        border-bottom: 1px solid #dfdfdf;
    }
    .airfly__card_time > .sep-ct > .line::after {
        content: '';
        border-bottom: 1px solid #dfdfdf;
        width: 8px;
        float: right;
        margin-top: -2px;
        transform: rotate(30deg);
    }
    .airfly__card_time > .sep-ct > .nonstop {
        margin-top: 5px;
        color: red;
    }
    /* .airfly__card_price----------------------------------- */
    .airfly__card_price {
        color: #ff8205;
        vertical-align: middle;
        margin-top: -10px;
        width: 200px;
        text-align: right;
    }
    .airfly__card_price > .prc {
        font-size: 30px;
    }
    .airfly__card_price > .prc > .rmb {
        font-size: 16px;
        font-family: Arial;
    }
    .airfly__card_price > .vim > .dis {
        color: #888;
        float: right;
    }
    /* .airfly__extends-------------------------------------- */
    .airfly__extends_list {
        border-top: 1px solid #cfcfcf;
    }
    .c-triangle {
        width: 0;
        height: 0;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #cfcfcf;
        float: right;
        margin: -10px 33px 0 0;
    }
    .c-triangle > p {
        width: 0;
        height: 0;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #f6f6f6;
        /* border-bottom: 10px solid #fdfdfd; */
        margin: 1px 0 0 -10px;
    }
    .flight-type {
        padding: 5px 0;
        background: #fdfdfd;
    }
    .flight-type > ul.tab {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
    }
    .flight-type > ul.tab > li {
        display: inline;
        margin: 0 20px;
        cursor: pointer;
        padding: 2px;
    }
    .flight-type > ul.tab > li.cur {
        color: #40bad6;
        border-bottom: 2px solid #40bad6;
        cursor: default;
    }
    .flight-info__list {
        height: 36px;
        padding: 0 30px;
        line-height: 36px;
        border-bottom: 1px solid #e1e1e1;
        background: #f6f6f6;
        clear: both;
    }
    .flight-info__list > ul {
        float: right;
        width: 48%;
        color: #555;
        text-align: right;
    }
    .flight-info__list > ul > li {
        display: inline;
        padding-left: 18px;
    }
    .flight-info__else {
        height: 50px;
        background: #f6f6f6;
    }
    .flight-info__else > p {
        font-size: 20px;
        text-align: center;
        padding-top: 10px;
    }
    .flight-info__order {
        background: #fdfdfd;
    }
    .flight-info__order > .order__card {
        padding: 0 30px;
        cursor: pointer;
    }
    .flight-info__order > .order__card > .order__card_col {
        border-bottom: 1px solid #ebebeb;
        padding: 11px 0;
        line-height: 30px;
        display: flex;
        justify-content: space-between;
    }
    .col-agent {
        min-height: 1px;
        width: 150px;
        color: #555;
        font-size: 16px;
    }
    .col-center {
        width: 650px;
        display: flex;
        justify-content: space-between
    }
    .col-center > span {
        font-size: 16px;
    }
    .col-center__tag {
        width: 250px;
    }
    .col-center > .col-center__tag > .col-center__tag_tips {
        display: inline-block;
        vertical-align: middle;
        color: #40bad6;
        margin-right: 10px;
    }
    .col-center > .col-center__prc {
        color: #bd1900;
        width: 400px;
        font-size: 24px;
    }
    .col-center__prc_logo {
        font-size: 16px;
    }
    .col-center__prc_stock, .col-center__prc_discount, .col-center__prc_ret {
        color: black;
        font-size: 14px;
    }
    .col-book > button {
        padding: 6px 20px;
    }
</style>



